svg.w-heatmap {
  -webkit-user-select: none;
          user-select: none;
  display: block;
}
svg.w-heatmap rect {
  display: block;
  cursor: pointer;
}
svg.w-heatmap rect:hover {
  stroke: rgba(0, 0, 0, 0.14);
  stroke-width: 1px;
}
svg.w-heatmap rect:active {
  fill: #196127;
  stroke-width: 0;
}
svg.w-heatmap text {
  text-anchor: middle;
  font-size: 9px;
  fill: currentColor;
}
